<template>
    <h1>Discord</h1>
    <input type="text" placeholder="Channel:" v-model="ChannelId" required/>
    <br />
    <br />
    <br />
    <br />
    <button v-on:click="getDiscord">Confirm</button>
</template>

<script>    
// import axios from "@/api/axios";
// var validator = require("email-validator");
// const qs = require('qs');

export default {
    name: "DiscordA",
    data() {
        return {
            ChannelId: null
        }
    },
    methods: {
        getDiscord() {
            if (this.ChannelId.length >= 17 && this.ChannelId.length <= 19) {
                let A = {
                    "channelId": this.ChannelId
                }
                let Action = {
                "content": JSON.stringify(A),
                    "id": "6360ec0fedf314cd1be57068"
                }
                let nameAct = {
                    "name": "Discord",
                    "description": "Send a message on a Discord server"
                }
                localStorage.action = JSON.stringify(Action);
                localStorage.nameact = JSON.stringify(nameAct);
                var myHeaders = new Headers();
                myHeaders.append("token", localStorage.token);

                var requestOptions = {
                  method: 'GET',
                  headers: myHeaders,
                  redirect: 'follow'
                };

                fetch(localStorage.server + "/user/discord/verify", requestOptions)
                    .then(res => {
                        res.json().then((ans) => {
                            if (ans["status"] == "ok") {
                              this.$router.push({ name: 'Recap' });
                            }
                            else {
                              localStorage.path = 'Recap';
                            
                              var requestOptions = {
                                method: 'GET',
                                headers: myHeaders,
                                redirect: 'follow'
                              };
                          
                              fetch(localStorage.server + "/user/discord/getLink", requestOptions)
                                .then(rep => {
                                    if (rep.status === 200) {
                                        rep.text().then((ans) => {
                                            window.location.href = ans;
                                        })
                                    }
                                })
                                .catch(error => console.log('error', error));
                            }
                        })
                    })
                  .catch(error => console.log('error', error));
                
            } else {
                alert('Discord IDs are between 17 and 19 numbers long.')
            }
        },
    }
}
</script>

